// Cache version - increment this when you want to force a cache clear
const CACHE_VERSION = '1';
const VERSION_PARAM = 'cv';
async function clearCaches() {
  if ('caches' in window) {
    try {
      const cacheNames = await caches.keys();
      const cachePromises = cacheNames.filter(name => name.includes('user-profile-app')).map(name => caches.delete(name));
      await Promise.all(cachePromises);
      console.log('Caches cleared');
    } catch (error) {
      console.warn('Error clearing caches:', error);
    }
  }
}
async function unregisterServiceWorkers() {
  if ('serviceWorker' in navigator) {
    try {
      const registrations = await navigator.serviceWorker.getRegistrations();
      for (const registration of registrations) {
        await registration.unregister();
      }
      console.log('Service workers unregistered');
    } catch (error) {
      console.warn('Error unregistering service workers:', error);
    }
  }
}
function clearStorages() {
  try {
    // Only clear storage items that might cause conflicts
    const clearKeys = [];
    for (let i = 0; i < localStorage.length; i++) {
      const key = localStorage.key(i);
      if (key && key.startsWith('dhis2')) {
        clearKeys.push(key);
      }
    }
    clearKeys.forEach(key => localStorage.removeItem(key));

    // Clear session storage
    sessionStorage.clear();
  } catch (error) {
    console.warn('Error clearing storage:', error);
  }
}

// Main initialization
export async function initialize() {
  // Skip in development
  if (process.env.NODE_ENV !== 'production') {
    return;
  }
  const url = new URL(window.location.href);
  const currentVersion = url.searchParams.get(VERSION_PARAM);

  // If version in URL doesn't match current version, update it
  if (currentVersion !== CACHE_VERSION) {
    // Store the current scroll position
    sessionStorage.setItem('scrollPosition', window.scrollY);

    // Update the version in URL without reloading if it's the first time
    if (!currentVersion) {
      url.searchParams.set(VERSION_PARAM, CACHE_VERSION);
      window.history.replaceState({}, '', url);
    } else {
      // If version exists but doesn't match, do a hard reload
      url.searchParams.set(VERSION_PARAM, CACHE_VERSION);
      window.location.replace(url.toString());
      return;
    }
  }

  // Clear caches in the background if needed
  if (!sessionStorage.getItem(`cacheCleared_${CACHE_VERSION}`)) {
    sessionStorage.setItem(`cacheCleared_${CACHE_VERSION}`, 'true');
    Promise.all([clearCaches(), unregisterServiceWorkers(), new Promise(resolve => {
      setTimeout(() => {
        clearStorages();
        resolve();
      }, 1000);
    })]).catch(console.error);
  }

  // If we get here, we're on the correct version
  console.log('Cache buster: Running version', CACHE_VERSION);
}
export default {
  initialize,
  CACHE_VERSION,
  VERSION_PARAM
};